.sidebar{
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
}

::-webkit-scrollbar{
    width: 5px;
}
::-webkit-scrollbar-track{
    background-color: #f1ccbb3f;
}

::-webkit-scrollbar-thumb{
    background-color: #f1ccbbcb;
}

.sidebarWrapper{
    /* background-color: yellowgreen; */
    padding: 20px;
}

.sidebarList{
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.sidebarIcon{
    margin-right: 15px;
}

.sidebarButton{
    width: 50px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 600;
}

.sidebarHr{
    margin:  20px 0;
}

.sidebarFriendList{
    margin: 0;
    padding: 0;
    list-style: none;
}

.sidebarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.sidebarFriendImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}